<template>
	<div v-theme:column="'narrow'" id="show-blogs">
		<h1>博客总览</h1>
		<input type="text" v-model="search" placeholder="搜索" />
		<div v-for="blog in filteredBlogs" class="single-blog">
			<router-link v-bind:to="'/blog/'+blog.id"><h2 v-rainbow="">{{blog.title | to-uppercase}}</h2></router-link>
			<article>
				{{blog.content | snippet}}
			</article>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'show-blogs',
		data() {
			return {
				blogs: [],
				search: ''
			}
		},
		created() {
			this.$http.get('https://my-blog-96629.firebaseio.com/posts.json').then(function(data) {
				//					console.log(data);
				return data.json()
//				this.blogs = data.body.slice(0, 10);
			
			})
			.then(function(data){
				var blogosArray=[];
				for (let key in data) {
					data[key].id=key;
					blogosArray.push(data[key]);
				}
				this.blogs=blogosArray;
			})
		},
		computed: {
			filteredBlogs: function() {
				return this.blogs.filter((blog) => {
					return blog.title.match(this.search)
				})
			}
		},
		filters: {
			'to-uppercase': function(value) {
				return value.toUpperCase();
			}
		}
//		directives:{
//			'rainbow':{
//				bind(el,binding,vnode){
//					el.style.color="red"
//				}
//			}
//		}
	}
</script>

<style>
	#show-blogs {
		max-width: 800px;
		margin: 0 auto;
	}
	
	.single-blog {
		padding: 20px;
		margin: 20px 0;
		box-sizing: border-box;
		background: #eee;
		border: 1px dotted #AAAAAA;
	}
	
	#show-blogs  a{
		color: #444444;
		text-decoration: none;
	}
	input[type="text"]{
			padding: 8px;
	        width: 100%;
		box-sizing: border-box;
	
		
	}
</style>


